<!-- by 添少 -->
<template>
  <fixed-layout>
    <ui-article>
      <template slot="title">{{title||'标题'}}</template>
      <template slot="summary">{{desc||'简介'}}</template>
      <section v-html="content||'内容'"></section>
    </ui-article>
    <template slot="footer">
      <section class="bar">
        <button class="primary" style="flex-basis:30%;" @click="handSubmit">发布{{contTypeName}}</button>
        <button @click="handSave">存草稿</button>
      </section>
    </template>
  </fixed-layout>
</template>
<script>
  import {mapState} from 'vuex'
  import FixedLayout from '@/components/fixed-layout'
  import UiArticle from '@/components/ui-article'

  export default {
    name: '',
    components: {
      FixedLayout,
      UiArticle
    },
    props: {},
    data() {
      return {}
    },
    watch: {},
    computed: {
      ...mapState({
        ContType: state => state.config.ContType,
        productId: state => state.write.article.productId,
        contType: state => state.write.article.contType,
        cover: state => state.write.article.cover,
        clubId: state => state.write.article.clubId,
        title: state => state.write.article.title,
        desc: state => state.write.article.desc,
        content: state => state.write.article.content,
      }),
      contTypeName() {
        return (this.ContType.find(e => e.value === this.contType) || {label: '文章'}).label
      },
    },
    beforeRouteLeave(to, from, next) {
      console.log(to)
      if (to.path.indexOf('/personalHomePage/draft') === -1 &&
        to.path.indexOf('/productArticleDetailsPage/') === -1 &&
        to.path.indexOf('/rendezvousPage/') === -1) {
        // 后退or其他
        next(`/rendezvousPage/${this.contType}`)
      } else {
        next()
      }
    },
    created() {
      this.init()
    },
    mounted() {
    },
    methods: {
      init() {
      },
      initData() {
      },
      saveArticle(type) {
        let params = {
          productId: this.productId,
          clubId: this.clubId,
          contType: this.contType,
          title: this.title,
          desc: this.desc,
          preimg: this.cover,
          cont: this.content,
          contStatus: type
        }
        this.$http.post(this.$port.ADD_PRODUCT, this.$qs.stringify(params)).then(res => {
          if (res.code === 200) {
            this.$store.commit(this.$actionType.set_article_data, {
              productId: res.data
            })
            if (type === 1) {
              // 草稿
              this.$router.push({
                path: `/personalHomePage/draft`,
              })
            } else if (type === 2) {
              // 发布
              this.$router.push({
                path: `/productArticleDetailsPage/${this.productId}`,
              })
            }
            this.$store.commit(this.$actionType.set_article_data, {
              productId: 0,
              contType: 1,
              cover: '',
              clubId: -1,
              title: '',
              desc: '',
              content: ''
            })
          } else if (res.code === 500) {
            alert(res.msg)
          }
        })
      },
      handSubmit() {
        this.saveArticle(2)
      },
      handSave() {
        this.saveArticle(1)
      }
    }
  }
</script>
<style lang="scss">

</style>
<style lang="scss" scoped>
  .fixed-layout {
    background-color: #fff;
  }

  .article {
    padding: 0 15px;
  }

  .bar {
    background-color: #fff;
    border-top: solid #1797c6 1px;
    display: flex;
    button {
      background-color: #fff;
      font-size: 18px;
      color: #38a6ce;
      padding: 10px 0;
      border: none;
      flex: 1;
      &.primary {
        background: linear-gradient(to bottom, #4cafd3, #1797c6);
        color: #fff;
      }
    }
  }

</style>



